'use client';
import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { signOut } from 'firebase/auth';
import { auth } from '../../firebase';
import { useMediaQuery } from 'react-responsive';
import logo from "../../assets/Logo.png";
import logoDoan from "../../assets/huyhieudoanq10.png"

function Navigation() {
  const isMobile = useMediaQuery({ query: '(max-width: 600px)' });
  const navigate = useNavigate();

  const handleLogout = () => {
    signOut(auth).then(() => {
      localStorage.removeItem('user');
      navigate('/login');
    }).catch((error) => {
      console.error('Logout error:', error);
    });
  };

  const imageStyles = {
    container: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: '10px', // This creates space between the images
    },
    logoDoan: {
      width: isMobile ? '30px' : '60px',
      height: isMobile ? '30px' : '60px',
    },
    logo: {
      width: isMobile ? '100px' : '200px',
      height: 'auto',
    },
  };

  return (
    <nav className={isMobile ? 'mobile-nav' : 'desktop-nav'} style={styles.nav}>
      <div style={styles.navContent}>
        <ul style={styles.navLinks}>
          <li style={styles.navItem}>
            <Link to="/" style={styles.link}>Trang chủ</Link>
          </li>
          <li style={styles.navItem}>
            <Link to="/detail" style={styles.link}>Chi tiết</Link>
          </li>
        </ul>
        <div style={imageStyles.container}>
          <img src={logoDoan} alt="Doan Logo" style={imageStyles.logoDoan} />
          <img src={logo} alt="Main Logo" style={imageStyles.logo} />
        </div>
        <button onClick={handleLogout} style={styles.logoutButton}>Đăng xuất</button>
      </div>
    </nav>
  );
}

const styles = {
  nav: {
    backgroundColor: '#add8e6',
    padding: '10px',
  },
  navContent: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  navLinks: {
    display: 'flex',
    listStyleType: 'none',
    padding: 0,
    margin: 0,
  },
  navItem: {
    marginRight: '20px',
  },
  link: {
    color: '#1877f2',
    textDecoration: 'none',
    transition: 'color 0.3s',
  },
  logoutButton: {
    backgroundColor: 'white',
    color: '#1877f2',
    border: 'none',
    padding: '4px 8px',
    borderRadius: '4px',
    cursor: 'pointer',
  },
};

export default Navigation;