import React, { useState, useEffect } from 'react';
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { MapContainer, TileLayer, Marker, useMapEvents, Polyline, useMap } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import './Form.css';
import customMarkerIcon from '../../assets/flag.png';
import * as Realm from "realm-web";

const app = new Realm.App({ id: process.env.REACT_APP_MONGODB_APP_ID });

const Form = () => {
  const userName = JSON.parse(localStorage.getItem('user'));
  const [currentPosition, setCurrentPosition] = useState(null);
  const [isDrawing, setIsDrawing] = useState(false);
  const [route, setRoute] = useState([]);
  const [location, setLocation] = useState({ lat: 10.762622, lng: 106.660172 });
  const [formData, setFormData] = useState({
    content: ''
  });
  const [listPosition, setListPosition] = useState([]);

  const customIcon = L.icon({
    iconUrl: customMarkerIcon,
    iconSize: [38, 38],
    iconAnchor: [19, 38],
    popupAnchor: [0, -38]
  })

  const [user, setUser] = useState(null);
  const storage = getStorage();

  useEffect(() => {
    const loginAnonymous = async () => {
      try {
        // Attempt to log in anonymously
        const user = await app.logIn(Realm.Credentials.anonymous());
        setUser(user);
      } catch (error) {
        console.error("Failed to log in", error);
      }
    };
    loginAnonymous();
  }, []);

  useEffect(() => {
    const handlePositionUpdate = (position) => {
      const { latitude, longitude } = position.coords;
      const newPosition = [latitude, longitude];
      setCurrentPosition(newPosition);
      if (isDrawing) {
        setRoute((prevRoute) => [...prevRoute, newPosition]);
      }
    };

    const handleError = (error) => {
      console.error("Error getting position: ", error);
    };

    navigator.geolocation.getCurrentPosition(handlePositionUpdate, handleError, {
      enableHighAccuracy: true,
      timeout: 2000,
      maximumAge: 0,
    });

    const watchId = navigator.geolocation.watchPosition(handlePositionUpdate, handleError, {
      enableHighAccuracy: true,
      timeout: 2000,
      maximumAge: 0,
    });

    return () => navigator.geolocation.clearWatch(watchId);
  }, [isDrawing]);

  // useEffect(() => {
  //   const getRoute = async () => {
  //     const user = await app.logIn(Realm.Credentials.anonymous());
  //     const mongodb = user.mongoClient("mongodb-atlas");
  //     const collection = mongodb.db("hanhtrinhxanh").collection("recordRoad");
  //     const locationsData = await collection.find();
  //     console.log(locationsData);
  //   }

  //   getRoute();
  // }, []);

  const MapCenter = () => {
    const map = useMap();
    useEffect(() => {
      if (listPosition.length > 0) {
        map.setView(listPosition[listPosition.length - 1], map.getZoom());
      }
    }, [listPosition, map]);
    return null;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) {
      console.error("No user logged in");
      return;
    }

    try {
      const mongodb = app.currentUser.mongoClient("mongodb-atlas");
      const collection = mongodb.db("hanhtrinhxanh").collection("locations");

      let imageUrl = null;
      if (formData.image) {
        const storageRef = ref(storage, 'images/' + formData.image.name);
        try {
          const snapshot = await uploadBytes(storageRef, formData.image);
          imageUrl = await getDownloadURL(snapshot.ref);
        } catch (uploadError) {
          console.error("Error uploading image: ", uploadError);
          alert("Error uploading image. Please try again.");
          return;
        }
      }

      await collection.insertOne({
        name: formData.name,
        job: formData.job,
        imageUrl: imageUrl,
        record: listPosition,
      });

      alert("Lưu hành trình thành công!");
      formData.clear();
    } catch (error) {
      alert("Lỗi thêm dữ liệu: ", error);
    }
  };

  const handleFileChange = (e) => {
    setFormData({ ...formData, image: e.target.files[0] });
  };

  const LocationMarker = () => {
    useMapEvents({
      click(e) {
        setLocation({ lat: e.latlng.lat, lng: e.latlng.lng });
      },
    });

    return location ?
      <Marker position={[location.lat, location.lng]} icon={customIcon} />
      : null;
  }

  const addPosition = () => {
    if (location) {
      setListPosition(prevPositions => [...prevPositions, [location.lat, location.lng]]);
      setRoute((prevRoute) => [...prevRoute, location]);
      console.log("Đã thêm vị trí:", location);
    } else {
      alert("Vui lòng đợi định vị để thêm vị trí.");
    }
  }

  const resetPosition = () => {
    setListPosition([]);
    setRoute([]);
    console.log("Reset positions");
  }

  return (
    <div className="form-display-container">
      <div className="content-wrapper">
        <div className="form-section">
          <form onSubmit={handleSubmit}>
            <div>
              <label>Tên</label>
              <input type="text" name="name" value={formData.name} onChange={handleInputChange} />
            </div>
            <div>
              <label>Chức vụ</label>
              <input type="text" name="job" value={formData.job} onChange={handleInputChange} />
            </div>
            <div>
              <label>Hành trình(tọa độ)</label>
              <textarea style={{ width: '100%', maxWidth: '300px', maxHeight: '150px' }} type="text" name="job" value={listPosition.join('=>')} readOnly />
            </div>
            <div>
              <label>Hình ảnh địa điểm:</label>
              <input type="file" onChange={handleFileChange} />
            </div>
            <button type="submit">Lưu hành trình</button>
          </form>
        </div>
        <div className="map-section">
          <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '10px' }}>
            <p>Kinh độ: {location.lat.toFixed(6)}</p>
            <p>Vĩ độ: {location.lng.toFixed(6)}</p>
            <div>
              <button onClick={addPosition} style={{ fontSize: '12px', padding: '5px' }}>Thêm</button>
              <button onClick={resetPosition} style={{ fontSize: '12px', padding: '5px' }}>Reset</button>
            </div>
          </div>
          <MapContainer center={currentPosition || [10.782625, 106.660678]} zoom={15} className="map-container">
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            />
            <Polyline positions={route} color="blue" />
            <LocationMarker />
            <MapCenter />
            {listPosition.map((position, index) => (
              <Marker
                key={index}
                position={position}
                icon={customIcon}
              />
            ))}
          </MapContainer>
        </div>
      </div>
    </div>
  );
};

export default Form;